<template>
  <div>
    <div class="container">
      <h1>Tipo de aeronave</h1>
    </div>
    <AirPlanetTypeTable :data="airPlaneTypeListState"
                        @DeleteAirPlaneType="DeleteAirPlaneType"
                        @addAirPlaneType="addAirPlaneType"
                        @updateAirplaneType="updateAirPlaneType"
                        :dataSquadron="SquadronListState"
    />
  </div>

</template>

<script>

import {mapActions, mapGetters} from 'vuex'
import AirPlanetTypeTable from "@/components/inventaryManagement/typeAirPlane/AirPlanetTypeTable";

export default {
  name: "AirPlaneTypeComponent",
  components: {
    AirPlanetTypeTable

  },
  props: ['inventaryDataItem',],
  data () {
    return {
      jsonDataUpdate: {
        abbreviation: 'string',
        autonomiaVuelo: 0,
        crew: 'string',
        crewNumber: 0,
        cruisespeed: 0,
        description: 'string',
        emptyWeight: 0,
        fuelExtreme: 0,
        fuelPerHour: 0,
        headline: 0,
        high: 0,
        id: 0,
        landingDistance: 0,
        length: 0,
        loadCapacity: 0,
        makerId: 0,
        maximumSpeed: 0,
        motorPower: 0,
        nominativo: 'string',
        operationCost: 0,
        passengerCapacity: 0,
        photo: 'string',
        projection: 0,
        quantityMotors: 0,
        roofFlight: 0,
        rotorDiameter: 0,
        squadronId: 0,
        status: 0,
        takeOffDistance: 0,
        timeTamp: '2022-04-19T21:22:07.730Z',
        totalFuel: 0,
        totalLoad: 0,
        type: 0,
        squadron: {
          id: 0,
        },
        typeJet: 0,
        width: 0,

      },
    }
  },

  created() {
    this.getAirPlaneTypeState()
    this.getSquadronState()
  },
  computed: {
    ...mapGetters(['airPlaneTypeListState', 'SquadronListState'])
  },
  methods: {
    ...mapActions(['getAirPlaneTypeState','getSquadronState','setAirPlaneTypStatenew', 'apiUpdateAirPlaneType', 'apiDeleteAirPlaneType']),
    addAirPlaneType( item) {
      this.setAirPlaneTypStatenew(item)
      this.getAirPlaneTypeState()
    },
    updateAirPlaneType( item) {
      this.jsonDataUpdate.abbreviation = item.abbreviation;
      this.jsonDataUpdate.crew = item.crew;
      this.jsonDataUpdate.nominativo = item.nominativo;
      this.jsonDataUpdate.description = item.description;
      this.jsonDataUpdate.id= item.id;
      this.jsonDataUpdate.squadron.id = item.squadron.id
      this.apiUpdateAirPlaneType(this.jsonDataUpdate)
    },
    DeleteAirPlaneType(id) {
      this.apiDeleteAirPlaneType(id)
    }
  }
}
</script>

