<template>
  <v-container>
    <v-data-table
        :headers="headers"
        :items="data"
        :search="search"
        sort-by="Tipo"
        class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar
            flat
        >
          <v-col
              cols="8"
              sm="4"
              md="2"
          >
            <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Buscar"
                single-line
                hide-details
            ></v-text-field>
          </v-col>

          <v-spacer></v-spacer>
          <v-dialog
              v-model="dialog"
              max-width="900px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
              >
                Añadir
              </v-btn>
            </template>
            <validation-observer
                ref="observer"
                v-slot="{ invalid }"
            >
              <form @submit.prevent="submit">
                <v-card>
                  <v-card-title>
                    <span class="text-h5">{{ formTitle }}</span>
                  </v-card-title>

                  <v-card-text>
                    <v-container>

                      <v-row>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="crew"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.crew"
                                :error-messages="errors"
                                label="Tipo"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="abbreviation"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.abbreviation"
                                :error-messages="errors"
                                label="abbreviation"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="nominativo"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.nominativo"
                                :error-messages="errors"
                                label="nominativo"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="description"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.description"
                                :error-messages="errors"
                                label="Descriptición"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="select"
                              rules="required"
                          >
                            <v-select
                                v-model="select"
                                :items="dataSquadron"
                                :error-messages="errors"
                                label="Selecionar escuadrón"
                                item-text="description"
                                item-value="id"
                                data-vv-name="select"
                                bottom
                                autocomplete
                                required
                            ></v-select>
                          </validation-provider>
                        </v-col>
                      </v-row>


                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="close"
                    >
                      Cancelar
                    </v-btn>
                    <v-btn
                        class="mr-4"
                        type="submit"
                        color="#002F6C"
                        style="color: #E5F0F4;"
                        :disabled="invalid"
                    >
                      Guardar
                    </v-btn>
                  </v-card-actions>
                  <br/>

                </v-card>
              </form>
            </validation-observer>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">¿Está seguro que desea eliminar?</v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">Cancelar</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
            small
            @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
  </v-container>

</template>

<script>
import { required,  max,  } from 'vee-validate/dist/rules'
import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate'
setInteractionMode('eager')

extend('required', {
  ...required,
  message: ' Campo no puede ir vacío',
})

extend('max', {
  ...max,
  message: ' No puede ser mayor que {length} caracteres',
})


export default {
  name:'AirPlanetTypeTable',
  components: {
    ValidationProvider,
    ValidationObserver,
  },
  props:['data', 'dataSquadron'],
  data: () => ({
    dialog: false,
    search: '',
    dialogDelete: false,
    updateAirplane: false,
    select: null,
    headers: [
      {
        text: 'ID',
        align: 'start',
        sortable: false,
        value: 'id',
      },
      { text: 'Tipo', value: 'crew' },
      { text: 'abbreviation', value: 'abbreviation' },
      { text: 'nominativo', value: 'nominativo' },
      { text: 'Descriptición', value: 'description' },
      { text: 'Actions', value: 'actions', sortable: false },
    ],
    desserts: [],
    editedIndex: -1,
    editedItem: {
      abbreviation: '',
      autonomiaVuelo: 0,
      crew: '',
      crewNumber: 0,
      cruisespeed: 0,
      description: '',
      emptyWeight: 0,
      fuelExtreme: 0,
      fuelPerHour: 0,
      headline: 0,
      high: 0,
      id: 0,
      landingDistance: 0,
      length: 0,
      loadCapacity: 0,
      makerId: 0,
      maximumSpeed: 0,
      motorPower: 0,
      nominativo: '',
      operationCost: 0,
      passengerCapacity: 0,
      photo: '',
      projection: 0,
      quantityMotors: 0,
      roofFlight: 0,
      rotorDiameter: 0,
      squadronId: 0,
      status: 0,
      takeOffDistance: 0,
      timeTamp: '',
      totalFuel: 0,
      totalLoad: 0,
      type: 0,
      squadron: {
        id: 0,
      },
      typeJet: 0,
      width: 0,

    },
    defaultItem: {
    },
  }),

  computed: {
    formTitle () {
      return this.updateAirplane ? 'Nuevo Avión ' : 'Editar Avión '
    },
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
  },

  methods: {
    submit () {
      if (this.$refs.observer.validate()) {
        this.editedItem.squadron.id = this.select
        this.save()
      } else {
        console.log('completar los campos')
      }

    },


    editItem (item) {
      this.editedItem = Object.assign({}, item)
      this.select = this.editedItem.squadron.id
      this.updateAirplane = true;
      this.dialog = true
    },

    deleteItem (item) {
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteItemConfirm () {
      this.$emit('DeleteAirPlaneType', this.editedItem.id)
      this.closeDelete()
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save () {

      if(this.updateAirplane) {
        this.$emit('updateAirplaneType', this.editedItem)
      } else {
        this.$emit('addAirPlaneType', this.editedItem)
      }
      this.updateAirplane = false
      this.close()
    },
  },
}
</script>
<style scoped>

</style>